import Moment from 'moment';
import React from 'react';
import './App.css';
import config from './config';

class App extends React.Component {
  constructor(...args) {
    super(...args);

    const query = this.parseQuery();
    const layoutConfig = this.getLayoutConfig(query, config);
    const time = Moment();

    this.state = { config, layoutConfig, query, time };

  }
  componentDidMount() {
    this.interval = setInterval(() => this.setState({ time: Moment() }), 5000);
  }
  componentWillUnmount() {
    clearInterval(this.interval);
  }
  render() {
    const layoutConfig = this.state.layoutConfig;
    if(!layoutConfig) {
      return (
        <div class="error">
          <h1>No layout specified</h1>
          <h3>Aailable layouts:</h3>
          <ul>{Object.keys(config.layouts).map(l => <li><a href={`/?layout=${l}`}>{l}</a></li>)}</ul>
        </div>
      );
    }
    return (
      <div className="root-inner">
        <div className={`App ${this.state.query.layout}`}>
          <div className="bg" style={{ 'background-image': `url(./${layoutConfig.bg || 'bg.jpg'})` }}></div>
          <header className="App-header">
            <div></div>
            <div className="text"><h1>taylorhub</h1></div>
            <div className="today">
              <div className="date">{this.state.time.format('dddd Do MMMM YYYY')}</div>
              <div className="time">{this.state.time.format('h:mma')}</div>
            </div>
          </header>
          <div className="widgets">
            {layoutConfig.widgets.map(w => this.renderWidget(w))}
          </div>
        </div>
      </div>
    );
  }
  getLayoutConfig(query, config) {
    try {
      const c = config.layouts[`${query.layout}`];
      if(!c) throw new Error();
      return c;
    } catch {
      console.error('No valid config found', query);
    }
  }
  parseQuery() {
    if(!window.location.search) {
      return {};
    }
    return window.location.search
      .slice(1)
      .split('&')
      .map(i => i.split('='))
      .reduce((m, [k, v]) => Object.assign(m, { [k]: v }), {});
  }
  renderWidget(w) {
    const data = { config: this.state.config, layoutConfig: this.state.layoutConfig };
    if(!Array.isArray(w)) {
      return React.createElement(w, data);
    }
    const wLen = w.length;
    if(!wLen || wLen > 5) {
      throw new Error(`Invalid layout size: ${wLen}`);
    }
    return (<div className={`widget-row-${wLen}`}>{w.map(w2 => React.createElement(w2, data))}</div>);
  }
}

export default App;
