import Moment from 'moment';
import React from 'react';
import List from './List';

import './styles/TechNews.css';

class News extends List {
  async getUpdateData() {
    return {
      name: 'technews',
      title: 'Tech news',
      items: await this.fetchJson('/api/twitter/tech', this.maxListItems)
    };
  }
  renderItemInner({ createdAt, text, user }) {
    return (
      <div className="ListItem compact">
        <div className="title">{text}</div>
        <div className="info">
          <div className="source">{user}</div>
          <div className="date">{Moment(createdAt).format('h:mma')}</div>
        </div>
      </div>
    );
  }
}

export default News;
