import Moment from 'moment';
import React from 'react';
import List from './List';

import './styles/News.css';

class News extends List {
  async getUpdateData() {
    return {
      name: 'news',
      title: 'Latest headlines',
      items: await this.fetchJson('/api/news', 5),
      updateInterval: 60000,
    };
  }
  renderItemInner({ date, headline, section }) {
    return (
      <div>
        <div className="title">{headline}</div>
        <div className="source">{section}</div>
        <div className="date">{Moment(date).format('h:mma')}</div>
      </div>
    );
  }
}

export default News;
