import Moment from 'moment';
import React from 'react';
import Widget from './Widget';

import './styles/Weather.css';

class Weather extends Widget {
  async getUpdateData() {
    return {
      name: 'weather',
      title: 'Weather',
      ...await this.fetchJson('/api/weather')
    };
  }
  renderInner() {
    if(!this.state.weather) {
      return;
    }
    return (
      <div className="Weather-inner">
        <div className="Weather-weather">
          {this.state.weather.map(e => React.createElement(WeatherSummary, e))}
        </div>
      </div>
    );
  }
}

function WeatherSummary(data) {
  const time = Moment(data.time);
  const tempMain = Math.round(data.tempMain);
  // const tempFeel = Math.round(data.tempFeel);
  return (
    <div className="Weather-summary">
      <div className="inner ListItem">
        <div className="time">{time.format('HH:mm')}</div>
        <img className="icon" src={data.icon} alt={data.description} />
        <div className="summary">{data.summary}</div>
        <div className="temp">{tempMain}<sup>o</sup>C</div>
        {/* <div className="feel">feels like {tempFeel}<sup>o</sup>C</div> */}
      </div>
    </div>
  );
}

export default Weather;
