import React from 'react';
import Widget from './Widget';

import './styles/MealsSmall.css';

class MealsSmall extends Widget {
  async getUpdateData() {
    return {
      name: 'meals-small',
      title: `Next week's meals`,
      meals: (await this.fetchJson('/api/meals?w=1')).slice(0, this.layoutConfig.maxListItems)
    };
  }
  renderInner() {
    if(!this.state.meals) {
      return;
    }
    return (
      <div className="Meals-meals">
        {this.state.meals.map((data, i) => React.createElement(Meal, { ...data, day: i+1 }))}
      </div>
    );
  }
}

function Meal({ name, day, icon, owner }) {
  return (
    <div className={`Meal small`}>
      <div className="name">{name}</div>
      <div className="owner">{owner}</div>
    </div>
  );
}

export default MealsSmall;
