import React from 'react';
import './styles/MealIcon.css';

import Burger from './styles/assets/mealicons/050-hamburguer.svg';
import Carrot from './styles/assets/mealicons/147-carrot.svg';
import Chicken from './styles/assets/mealicons/082-turkey.svg';
import Curry from './styles/assets/mealicons/127-rice.svg';
import Default from './styles/assets/mealicons/081-food.svg';
import Fish from './styles/assets/mealicons/135-fish.svg';
import JacketPotato from './styles/assets/mealicons/145-potatoes.svg';
import Pasta from './styles/assets/mealicons/203-spaguetti.svg';
import Pizza from './styles/assets/mealicons/104-pizza.svg';
import Sausage from './styles/assets/mealicons/108-sausage.svg';
import Steak from './styles/assets/mealicons/086-steak.svg';
import Wrap from './styles/assets/mealicons/101-kebab.svg';

const Icons = {
  Burger: { icon: Burger, alt: "A hamburger" },
  Carrot: { icon: Carrot, alt: "A carrot" },
  Chicken: { icon: Chicken, alt: "A roast chicken" },
  Curry: { icon: Curry, alt: "A bowl of curry" },
  Default: { icon: Default, alt: "A tasty dish" },
  Fish: { icon: Fish, alt: "A fish" },
  JacketPotato: { icon: JacketPotato, alt:  "A potato" },
  Pasta: { icon: Pasta, alt: "A bowl of pasta" },
  Pizza: { icon: Pizza, alt: "A pizza" },
  Sausage: { icon: Sausage, alt: 'A big juicy sausage!' },
  Steak: { icon: Steak, alt: 'A big juicy steak!' },
  Wrap: { icon: Wrap, alt: "A tasty wrap" },
};

class MealIcon extends React.Component {
  constructor({ name }) {
    super();
    this.state = { name };
  }
  render() {
    const { alt, icon } = Icons[this.state.name] || Icons.Default;
    return <div className="MealIcon"><img src={icon} alt={alt} /></div>;
  }
}

export default MealIcon;
