import MealIcon from './MealIcon.js';
import Moment from 'moment';
import React from 'react';
import Widget from './Widget';

import './styles/Meals.css';

class Meals extends Widget {
  async getUpdateData() {
    return {
      name: 'meals',
      title: `This week's meals`,
      meals: await this.fetchJson('/api/meals')
    };
  }
  renderInner() {
    if(!this.state.meals) {
      return;
    }
    return (
      <div className="Meals-meals">
        {this.state.meals.map((data, i) => React.createElement(Meal, { ...data, day: i+1 }))}
      </div>
    );
  }
}

function Meal({ name, day, icon, owner }) {
  const isToday = Moment().isoWeekday() === day;
  return (
    <div className={`Meal ${isToday ? 'today' : ''}`}>
      <div className="day">{Moment().isoWeekday(day).format('ddd')}</div>
      <MealIcon name={icon} />
      <div className="name">{name}</div>
      <div className="owner">{owner}</div>
    </div>
  );
}

export default Meals;
