import React from 'react';
import Widget from './Widget';

import './styles/GitHub.css';

class GitHubStats extends Widget {
  async getUpdateData() {
    return {
      name: 'githubstats',
      data: await this.fetchJson('/api/github/stats')
    };
  }
  renderInner() {
    if(!this.state.data) {
      return;
    }
    const { contributions, status, login, location } = this.state.data;
    return (
      <div>
        <h3 className="GitHub-user">{login}</h3>
        <div className="GitHub-location">{location}</div>
        <div className="GitHub-status">
          <span className="GitHub-icon" dangerouslySetInnerHTML={{__html: status.emojiHTML}}></span>
          {status.message}
        </div>
        <div className="GitHub-contributions">{contributions} contributions</div>
      </div>
    );
  }
}

export default GitHubStats;
