import Fetch from 'node-fetch';
import React from 'react';
import './styles/Widget.css';

class Widget extends React.Component {
  constructor({ config, layoutConfig }) {
    super();
    this.config = config;
    this.layoutConfig = layoutConfig;
    this.interval = null;
    this.state = {
      autoUpdate: true,
      updateInterval: this.config.updateInterval
    };
  }
  componentDidMount() {
    this.updateData();
  }
  async updateData() {
    try {
      this.setState(await this.getUpdateData());
    } catch(e) {
      console.error(e);
    }
    if(!this.interval && this.state.autoUpdate) {
      this.interval = setInterval(this.updateData.bind(this), this.state.updateInterval);
    }
  }
  async getUpdateData() {
    return {};
  }
  async fetchJson(url, limit = 0) {
    const response = await Fetch(url);
    const data = await response.json();
    const e = new Event('debug');
    e.data = { url, time: new Date().toISOString(), json: data };
    window.dispatchEvent(e);
    return limit > 0 ? data.slice(0, limit) : data;
  }
  render() {
    const title = this.state.title;
    const name = this.state.name;
    return (
      <div className={`Widget ${name}`}>
        <header className="Widget-header">
          {title ? <h1>{title}</h1> : ''}
        </header>
        <div className="Widget-inner">
          {this.renderInner()}
        </div>
      </div>
    );
  }
  renderInner() {
    // to be implemented by subclasses
  }
}

export default Widget;
