import Moment from 'moment';
import React from 'react';
import Widget from './Widget';

import './styles/Events.css';

class Events extends Widget {
  async getUpdateData() {
    return {
      name: 'events',
      title: 'Upcoming events',
      events: await this.fetchJson('/api/events', this.layoutConfig.maxListItems)
    };
  }
  renderInner() {
    if(this.state.events) {
      return (<div className="Events-events">{this.state.events.map(e => React.createElement(Event, e))}</div>);
    }
  }
}

function Event({ summary, start, end }) {
  const startDate = Moment(start.dateTime || start.date);
  const endDate = Moment(end.dateTime || end.date);
  return (
    <div className="Event">
      <div className="date">
        <span className="month">{startDate.format('D')}/{startDate.format('M')}</span>
        <span className="day">{startDate.format('ddd')}</span>
      </div>
      <div className="info">
        <span className="time">{calculateDuration(startDate, endDate)}</span>
        <span className="title">{summary}</span>
      </div>
    </div>
  );
}

function calculateDuration(startDate, endDate) {
  const duration = Moment.duration(endDate.diff(startDate));
  if(duration.asHours() > 24) {
    return `For ${duration.asDays()} days`;
  }
  if(duration.asHours() === 24) {
    return 'All day';
  }
  const startVal = startDate.format(startDate.minute() === 0 ? 'ha' : 'h:mma');
  const endVal = endDate.format(endDate.minute() === 0 ? 'ha' : 'h:mma');
  return `${startVal} - ${endVal}`;
}

export default Events;
