import React from 'react';
import Widget from './Widget';

class UselessFact extends Widget {
  async getUpdateData() {
    const { text } = await this.fetchJson('https://uselessfacts.jsph.pl/today.json?language=en');
    return {
      name: 'fact',
      title: `Useless fact`,
      autoUpdate: true,
      fact: text,
      updateInterval: 1000*60*60*12,
    };
  }
  renderInner() {
    return (<div className="ListItem">{this.state.fact}</div>);
  }
}

export default UselessFact;
