import React from 'react';
import Widget from './Widget';

import './styles/GitHub.css';

class GitHubIssues extends Widget {
  async getUpdateData() {
    const { issues, totalCount } = await this.fetchJson('/api/github/issues');
    return {
      name: 'githubissues',
      title: `GitHub issues`,
      issues: issues.slice(0, this.layoutConfig.maxListItems),
      totalCount
    };
  }
  renderInner() {
    if(!this.state.issues) {
      return;
    }
    const { issues, totalCount } = this.state;
    const more = totalCount - issues.length;
    return (
      <div className="GitHub-issues">
        <div className="GitHub-issues">
          {issues.map(this.renderIssue)}
          {more && <div className="more">+ {more} more</div>}
        </div>
      </div>
    );
  }
  renderIssue({ author, number, repository, title, url }) {
    return (
      <div className="ListItem compact">
        <div className="title"><a href={url} target="_blank">#{number} {title}</a></div>
        <div className="info">
          <div className="repo">{repository}</div>
          <div className="author">{author}</div>
        </div>
      </div>
    )
  }
}

export default GitHubIssues;
