import React from 'react';
import Widget from './Widget';

import './styles/Debug.css';

class Debug extends Widget {
  constructor(...args) {
    super(...args);
    this.state = { 
      autoUpdate: false,
      messages: []
    };
  }
  componentDidMount() {
    super.componentDidMount();
    window.addEventListener('debug', e => {
      let messages = this.state.messages;
      if(messages.length > 2) messages = messages.slice(0, -1);
      this.setState({ messages: [`${e.data.time} ${e.data.url}`, ...messages] });
    });
  }
  async getUpdateData() {
    return {
      name: 'debug',
      title: `Debug`
    };
  }
  renderInner() {
    return <div className="Debug-message">{this.state.messages.map(m => <p>{m}</p>)}</div>
  }
}

export default Debug;
