import React from 'react';
import Widget from './Widget';

import RecyclingIcon from './styles/assets/recycling.svg';
import WasteIcon from './styles/assets/trash.svg';
import './styles/BinDay.css';

class BinDay extends Widget {
  async getUpdateData() {
    return {
      name: 'binday',
      title: 'Bin', 
      isRecycling: await this.fetchJson('/api/binday')
    };
  }
  renderInner() {
    const icon = <img src={this.state.isRecycling ? RecyclingIcon : WasteIcon} alt="bin icon"/>;
    const label = this.state.isRecycling ? 'Recycling' : 'Household Waste';
    const className = `label ${this.state.isRecycling ? 'green' : ''}`;
    return [
      <div className="icon">{icon}</div>,
      <div className={className}>{label}</div>,
    ];
  }
}

export default BinDay;