import React from 'react';
import Widget from './Widget';

import './styles/List.css';

class List extends Widget {
  renderInner() {
    if(!this.state.items || !this.state.items.length) {
      return (<span className="List-empty">Nothing to see here!</span>);
    }
    const maxItems = this.layoutConfig.maxListItems;
    let items = this.state.items;
    let hiddenCount = 0;

    if(this.state.items.length > maxItems) {
      items = this.state.items.slice(0, maxItems-1);
      hiddenCount = this.state.items.slice(maxItems-1).length
    }
    return (
      <div className="List-items">
        {items.map(i => (<div className="ListItem">{this.renderItemInner(i)}</div>))}
        {hiddenCount ? (<div className="ListItem hidden">+ {hiddenCount} more item{hiddenCount > 1 ? 's' : ''}</div>) : ''}
      </div>
    );
  }
  renderItemInner(itemData) {
    // to be implemented by subclass
  }
}

export default List;
